                isLiked: false
            };
            
            this.comments.unshift(newComment);
            this.newComment = '';
            this.isCommentExpanded = false;
            
            uni.showToast({
                title: 'ËØÑËÆ∫ÊàêÂäü',
                icon: 'success'
            });
        },
        
        toggleLike(index) {
            if (this.isLiking) return;
            this.isLiking = true;
            
            const comment = this.comments[index];
            if (comment.isLiked) {
                comment.likeCount--;
            } else {
                comment.likeCount++;
            }
            comment.isLiked = !comment.isLiked;
            
            setTimeout(() => {
                this.isLiking = false;
            }, 300);
        },
        
        confirmDeleteComment(index) {
            uni.showModal({
                title: 'Á°ÆËÆ§Âà†Èô§',
                content: 'Á°ÆÂÆöË¶ÅÂà†Èô§ËøôÊù°ËØÑËÆ∫ÂêóÔºü',
                success: (res) => {
                    if (res.confirm) {
                        this.comments.splice(index, 1);
                        uni.showToast({
                            title: 'Âà†Èô§ÊàêÂäü',
                            icon: 'success'
                        });
                    }
                }
            });
        }
    }
}
</script>
